    text-decoration: none;
}
.navigation-single {
    margin-bottom: 1.2em;
}
.navigation-single .next,
.navigation-single .prev {
    float: left;
    vertical-align: middle;
    max-width: 50%;
    background: var(--light-grey);
    color: var(--third-color);
    text-decoration: none;
}
.navigation-single .next a,
.navigation-single .prev a {
